import React from 'react';
import "./PostModal.css";
import CloseIcon from '@mui/icons-material/Close';
import CommentIcon from '@mui/icons-material/Comment';
import { useState } from 'react';


const PostModal = (props) => {
    const [editorText, setEditorText]=useState("");
    const [shareImage,setShareImage]=useState(" ");


    const handlechange=(e)=>{
        const image= e.target.files[0];
        if (image === " " || image=== undefined){
            alert(`not an image this files is ${typeof image}`)
            return;
        }
       setShareImage(image); 


    }
    const reset =(e)=> {
        setEditorText("");
        props.handleClick(e);
    }
    
  return (
    <>
    {props.showModal === "open" && (
    <div className='postmodal'>
        <div className='content'>
            <div className='content-sub'>
                <h2> Create a Post </h2>
                <button onClick={(event)=>reset(event)}>
                    <CloseIcon/>
                    
                </button>
            </div>
            <div className='shared-content'>
                
                <img src="beard.jpeg"/>
                <span>Name</span>
                <div className='editor'>
                <textarea value={editorText} onChange={(e=>setEditorText(e.target.value))}
                placeholder="what do you want to discuss" autoFocus={true}></textarea> </div>

                <div className='upload-image'>
                    <input type="file" accept='image/jpeg , image/gif, image/png' name="image" id="file"
                    style={{display: "none"}} onChang={handlechange}/>
                    <p> <span>
                        <label htmlFor='file'> select an image to share</label></span>
                    </p>
                    {shareImage && <img src={URL.createObjectURL(shareImage)}/>}
                </div>
            </div>
            <div className='shared-actions'>
                <div className='action-button'>
                    <button>
                    <img src='video.png'/>
                  
                    </button>
                    <button>
                        <img src="gallery.png"/>
                    </button>
                   
                        
                </div>
                <div className='shared-comment'>
                    <button>
                        <CommentIcon/> Anyone
                    </button>

                </div>
                <div className='post-button'>
                    <button>
                        Post
                    </button>
                </div>
            </div>
        </div>
        



        </div>
    )
   
}
        </>
  );
};

export default PostModal;